// 拦截器
import axios from "axios";
import router from "@/router";
import { Message } from "element-ui";
/**
 * 配置全局参数请求头
 */
const $axios = axios.default.create({
  withCredentials: true,
  headers: { "X-Requested-With": "XMLHttpRequest" }
});

/**
 * 请求拦截器
 */

$axios.interceptors.request.use(
  request => {
    // 将Token携带在请求中
    const tokenId = sessionStorage.getItem("tokenId") || "";
    request.headers.token = tokenId;
    request.headers.common["Authorization"] = tokenId;
    return request;
  },
  error => {
    return Promise.reject(error);
  }
);

/** 响应拦截器 */
$axios.interceptors.response.use(
  response => {
    if (response.data.code == "401") {
      router.push("/login");
      return false;
    }
    if (response.data.code == "402") {
      this.$notify("修改密码", response.data.msg, "error");
      return false;
    }
    return response.data;
  },
  error => {
    return Promise.reject(error);
  }
);

/**
 * 根据传入的自定义参数，返回对应接口所返回的数据
 * @param {Object} data      接口返回的数据
 * @param {Boolean} custom   是否返回完整的数据，否则返回具体的json（不包括code,message）
 */

function extractData(data, custom) {
  const body = JSON.parse(data);

  if (custom) {
    return body;
  } else {
    if (body.code === "000000") {
      return body.data;
    } else {
      Message({
        message: body.msg,
        type: "error",
        showClose: true
      });
      return body;
    }
  }
}
export default {
  ...$axios,
  get(url, params, custom = false) {
    return $axios({
      method: "get",
      url,
      params,
      transformResponse: [
        function(data) {
          return extractData(data, custom);
        }
      ]
    });
  },
  post(url, params, custom = false, config) {
    return $axios({
      method: "post",
      url,
      data: params,
      config,
      transformResponse: [
        function(data) {
          return extractData(data, custom);
        }
      ]
    });
  }
};
