<template>
  <div class="login-all-con">
    <el-form
      ref="form"
      :model="formData"
      :rules="rules"
      class="form"
      label-width="80px"
    >
      <el-form-item label="用户名" prop="username">
        <el-input v-model="formData.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input v-model="formData.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" @click="login"
          >登录</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import rules from "@/rules";
import { loginApi } from "@/api/auth";
export default {
  data() {
    return {
      formData: {
        username: "",
        password: ""
      },
      rules: {
        username: [rules.required()]
      }
    };
  },
  methods: {
    login() {
      this.$refs.form.validate(valid => {
        if (valid) {
          loginApi(this.formData).then(res => {
            if (res.code === 200) {
              this.$routerLink("/chat", { username: this.formData.username });
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.login-all-con {
  min-height: 900px;
  display: flex;
  align-items: center;
  justify-content: center;
  .form {
    width: 400px;
  }
}
</style>
