/** 此处封装一些全局的方法 */
import router from "@/router/index";
import { Notification, MessageBox, Message } from "element-ui";
export default {
  install(Vue) {
    Vue.prototype.$confirm = MessageBox.confirm;
    Vue.prototype.$alert = MessageBox.alert;
    Vue.prototype.$message = function(
      message,
      type = "success",
      showClose = true
    ) {
      Message({
        message,
        type,
        showClose
      });
    };
    Vue.prototype.$notify = function(
      title,
      message,
      type = "success",
      showClose = true
    ) {
      Notification({
        title,
        message,
        type,
        showClose
      });
    };
    Vue.prototype.$routerLink = function(path, queryParams) {
      router.push({ path, query: queryParams });
    };

    Vue.prototype.$debounce = function debounce(func, delay) {
      let timer = null;
      return function(...args) {
        if (timer) {
          clearTimeout(timer);
        }
        timer = setTimeout(() => {
          func.apply(this, args);
        }, delay);
      };
    };
  }
};
